import React, { useState, useEffect } from 'react';
import * as styles from './workV2.module.css';

import Banner from '../components/Banner';
import Breadcrumbs from '../components/Breadcrumbs';
import CardController from '../components/CardController';
import Container from '../components/Container';
import Layout from '../components/Layout';
import ProductCardGrid from '../components/ProductCardGrid';
import { generateMockProductData } from '../helpers/mock';
import Config from '../config.json';

const WorkPage = (props) => {
  const [showFilter, setShowFilter] = useState(false);
  const data = generateMockProductData(4, 'woman');

  useEffect(() => {
    window.addEventListener('keydown', escapeHandler);
    return () => window.removeEventListener('keydown', escapeHandler);
  }, []);

  const escapeHandler = (e) => {
    if (e?.keyCode === undefined) return;
    if (e.keyCode === 27) setShowFilter(false);
  };

  return (
    <Layout>
      <div className={styles.root}>
        <Container size={'large'} spacing={'min'}>
          <div className={styles.breadcrumbContainer}>
            <Breadcrumbs
              crumbs={[
                { link: '/', label: 'Home' },
                { label: 'Work' },
              ]}
            />
          </div>
        </Container>
        <Banner
          maxWidth={'650px'}
          name={`Work`}
          subtitle={
            'Lorem ipsum dolor sit amet, consectetur adipiscing elit. Etiam diam urna, maximus eu varius eget, tincidunt a felis. Phasellus eu odio erat. Curabitur nec turpis libero.'
          }
        />
        <Container size={'large'} spacing={'min'}>
          <CardController
            closeFilter={() => setShowFilter(false)}
            visible={showFilter}
            filters={Config.filters}
          />
          <div className={styles.productContainer}>
            <span className={styles.mobileItemCount}>Lorem ipsum dolor sit amet</span>
            <ProductCardGrid columns={4} data={data}></ProductCardGrid>
          </div>
        </Container>
      </div>

    </Layout>
  );
};

export default WorkPage;
